.cv {
  width: 23cm;
  margin: 0 auto;
  padding: 1cm;
  border: 1px #d3d3d3 solid;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  background-image: url('https://cv.fullstack.edu.vn/backgrounds/graph-dot-top-458966.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.cv .page {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-align: left;
  width: 21cm;
  min-height: 29.7cm;
  box-sizing: border-box;
  border-radius: 5px;
  background: url('https://cv.fullstack.edu.vn/backgrounds/graph-dot-top-458966.svg');
}

.page + .page {
  margin-top: 20px;
}

.cv .img-container:hover {
  cursor: pointer;
  opacity: 0.7;
}
